.content {
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  height: 100%;
  width: 100%;
  padding-bottom: 1.333333rem;
}

.footer {
  width: 100%;
  height: 1.333333rem;
  background: #fff;
  position: fixed;
  bottom: 0;
  color: red;
  text-align: center;
  font-size: 0.48rem;
  line-height: 1.333333rem;
}

.item {
  display: -webkit-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

img {
  width: 100%;
  height: auto;
}

.title {
  width: 100%;
  font-size: 0.426667rem;
  display: block;
  text-align: center;
  padding: 0.133333rem;
}

.loading {
  height: 0.666667rem;
  line-height: 0.666667rem;
  text-align: center;
  font-size: 0.426667rem;
}

.floor {
  height: 0.666667rem;
  line-height: 0.666667rem;
  text-align: center;
  font-size: 0.426667rem;
  color: pink;
}
